body {
  background-color: plum;
  gap: 15px;
  padding: 25px;
  display: flex;
}

p.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  color: #8a2be2;
  border: 5px solid #fff;
  border-radius: 50%;
  animation: pulse 1s linear infinite;
  display: inline-block;
  position: relative;
}

p.loader:after {
  content: "";
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  color: #8a2be2;
  border: 5px solid #fff;
  border-radius: 50%;
  animation: scaleUp 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes scaleUp {
  0% {
    transform: translate(-50%, -50%)scale(0);
  }

  60%, 100% {
    transform: translate(-50%, -50%)scale(1);
  }
}

@keyframes pulse {
  0%, 60%, 100% {
    transform: scale(1);
  }

  80% {
    transform: scale(1.2);
  }
}

div.cat-info {
  width: 75%;
  height: 100%;
  gap: 15px;
  display: flex;
}

div.cat-info img {
  width: 40%;
  height: 40%;
}

div.cat-info p {
  width: 100%;
  height: 100%;
  color: gray;
}

div.cat-info strong {
  width: 100%;
  height: 100%;
  color: purple;
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

select {
  width: 150px;
  height: 25px;
  margin: 25px 0 100px;
}

